// Font Sizes
.fs12 {
  font-size: rem(12px);
}
.fs14 {
  font-size: rem(14px);
}
.fs18 {
  font-size: rem(18px);
}
.fs20 {
  font-size: rem(20px);
}
.fs24 {
  font-size: rem(24px);
}
.fs28 {
  font-size: rem(28px);
}
.fs30 {
  font-size: rem(30px);
}

// flex helpers
.flex {
  display: flex;

  > *.full {
    flex: 1;
  }
  &.column {
    flex-direction: column;
  }
  &.justify-center {
    justify-content: center;
  }
  &.justify-space-between {
    justify-content: space-between;
  }
  &.align-center {
    align-items: center;
  }
  &.wrap {
    flex-wrap: wrap;
  }
  &.column-reverse {
    flex-direction: column-reverse;
  }
}
.empty {
  // width: 10px;
  opacity: 0;
}

.grid {
  display: grid;
}

.current {
  background-color: $clrP;
  border-radius: 50%;
}

.dark {
  color: $clrW;
}

// separator horizontal
.separator {
  width: 100%;
  height: 1px;

  margin: 8px 0;
}
